import React, { MouseEvent, useEffect, useRef, useState } from "react";
import SuperButton from "../hw04/common/c2-SuperButton/SuperButton";
import { restoreState } from "../hw06/localStorage/localStorage";
import s from "./Clock.module.css";

function Clock() {
  const [timerId, setTimerId] = useState<number | undefined>(undefined);
  // for autotests //   //      ,     
  const [date, setDate] = useState<Date>(
    new Date(restoreState("hw9-date", Date.now()))
  );
  const [show, setShow] = useState<boolean>(false);
  console.log(timerId);
  const start = () => {
    //   //   (   ,   +1)
    //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)

    const timerID = window.setInterval(() => {
      setDate(new Date());
    }, 1000);
    setTimerId(timerID);
  };

  const stop = () => {
    clearInterval(timerId);
    setTimerId(undefined);
    //   //    ,    (timerId <- undefined)
  };

  const onMouseEnter = () => {
    setShow(true);
  };
  const onMouseLeave = () => {
    setShow(false);
  };

  //   const stringTime = "date->time" || <br />; // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  
  const stringTime = `${date.getHours()}:${String(date.getMinutes()).padStart(
    2,
    "0"
  )}:${String(date.getSeconds()).padStart(2, "0")}` || <br />; // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  
  //   const stringDate = "date->date" || <br />; // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  
  const stringDate = `${String(date.getDate()).padStart(2, "0")}.${String(
    date.getMonth() + 1
  ).padStart(2, "0")}.${date.getFullYear()}` || <br />; // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  

  //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
  const daysOfWeek = [
    "Sunday",
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
  ];
  const stringDay = daysOfWeek[date.getDay()];
  //   const stringMonth = date.getMonth() || <br />; //  

  const months = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ];
  const stringMonth = months[date.getMonth()] || <br />; //  

  return (
    <div className={s.clock}>
      <div
        id={"hw9-watch"}
        className={s.watch}
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
      >
        <span id={"hw9-day"}>{stringDay}</span>,{" "}
        <span id={"hw9-time"}>
          <strong>{stringTime}</strong>
        </span>
      </div>

      <div id={"hw9-more"}>
        <div className={s.more}>
          {show ? (
            <>
              <span id={"hw9-month"}>{stringMonth}</span>,{" "}
              <span id={"hw9-date"}>{stringDate}</span>
            </>
          ) : (
            <>
              <br />
            </>
          )}
        </div>
      </div>

      <div className={s.buttonsContainer}>
        <SuperButton
          id={"hw9-button-start"}
          disabled={timerId ? true : false} //   //    
          onClick={start}
        >
          start
        </SuperButton>
        <SuperButton
          id={"hw9-button-stop"}
          disabled={timerId ? false : true} //   //     
          onClick={stop}
        >
          stop
        </SuperButton>
      </div>
    </div>
  );
}

export default Clock;
