import React, { useEffect, useState } from "react";
import s2 from "../../s1-main/App.module.css";
import s from "./HW15.module.css";
import axios from "axios";
import SuperPagination from "./common/c9-SuperPagination/SuperPagination";
import { useSearchParams } from "react-router-dom";
import SuperSort from "./common/c10-SuperSort/SuperSort";

/*
 * 1 -  SuperPagination
 * 2 -  SuperSort
 * 3 -  pureChange 
 * 3 -  sendQuery, onChangePagination, onChangeSort  HW15
 * 4 -      
 * 5 -  HW15  HW5/pages/JuniorPlus
 * */

type TechType = {
  id: number;
  tech: string;
  developer: string;
};

type ParamsType = {
  sort: string;
  page: number;
  count: number;
};

const getTechs = (params: ParamsType) => {
  return axios
    .get<{ techs: TechType[]; totalCount: number }>(
      "https://samurai.it-incubator.io/api/3.0/homework/test3",
      { params }
    )
    .catch((e) => {
      alert(e.response?.data?.errorText || e.message);
    });
};

const HW15 = () => {
  const [sort, setSort] = useState("");
  const [page, setPage] = useState(1);
  const [count, setCount] = useState(4);
  const [idLoading, setLoading] = useState(false);
  const [totalCount, setTotalCount] = useState(100);
  const [searchParams, setSearchParams] = useSearchParams();
  const [techs, setTechs] = useState<TechType[]>([]);

  const sendQuery = (params: any) => {
    setLoading(true);
    getTechs(params).then((res) => {
      console.log(res);

      setTechs(res ? res.data.techs : []);
      setTotalCount(res ? res.data.totalCount : 100);
      setLoading(false);
      //  

      //   

      //
    });
  };

  const onChangePagination = (newPage: number, newCount: number) => {
    // console.log(newPage, newCount);
    //  
    setPage(newPage);
    setCount(newCount);
    sendQuery({ page: newPage, count: newCount });
    // setSearchParams(
    //
  };

  //   const onChangeSort = (newSort: string) => {
  //     //  
  //     // setSort(
  //     // setPage(1) //     1 
  //     // sendQuery(
  //     // setSearchParams(
  //     //
  //   };
  const onChangeSort = (newSort: string) => {
    setSort(newSort); //   
    setPage(1); //    
    sendQuery({ sort: newSort, page: 1, count }); //   
    setSearchParams({ sort: newSort, page: "1", count: String(count) }); //  
  };

  useEffect(() => {
    const params = Object.fromEntries(searchParams);
    sendQuery({ page: params.page, count: params.count });
    setPage(+params.page || 1);
    setCount(+params.count || 4);
  }, []);

  const mappedTechs = techs.map((t) => (
    <div key={t.id} className={s.row}>
      <div id={"hw15-tech-" + t.id} className={s.tech}>
        {t.tech}
      </div>

      <div id={"hw15-developer-" + t.id} className={s.developer}>
        {t.developer}
      </div>
    </div>
  ));

  return (
    <div id={"hw15"}>
      <div className={s2.hwTitle}>Homework #15</div>

      <div className={s2.hw}>
        {idLoading && (
          <div id={"hw15-loading"} className={s.loading}>
            Loading...
          </div>
        )}

        <SuperPagination
          page={page}
          itemsCountForPage={count}
          totalCount={totalCount}
          onChange={onChangePagination}
        />

        <div className={s.rowHeader}>
          <div className={s.techHeader}>
            tech
            <SuperSort sort={sort} value={"tech"} onChange={onChangeSort} />
          </div>

          <div className={s.developerHeader}>
            developer
            <SuperSort
              sort={sort}
              value={"developer"}
              onChange={onChangeSort}
            />
          </div>
        </div>

        {mappedTechs}
      </div>
    </div>
  );
};

export default HW15;
